  @include centerContainer;\n            border-top: 2px solid #eeeeee3b;\n            .arrowDownIcon {\n                transition: transform 0.3s;\n                font-size: 32px;\n                color: #eee;\n            }\n        }\n        &.GroupRankGumblersList-expanded {\n            .GroupRankGumblersList-expandIconContainer {\n                .arrowDownIcon {\n                    transform: rotate(180deg);\n                }\n            }\n        }\n    }\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;"],"sourceRoot":""}]);
