us: 0 0 12px 12px;\n            .MuiTable-root  {\n                border-collapse: separate;\n            }\n            .tableHeaderCell {\n                padding: 8px 16px;\n                font-size: 16px;\n                font-weight: 700;\n            }\n            .LigaBet-CustomTableRow {\n                @include themable_prize_row(prize-theme-1, $prize_1);\n                @include themable_prize_row(prize-theme-2, $prize_2);\n                @include themable_prize_row(prize-theme-3, $prize_3);\n                @include themable_prize_row(prize-theme-4, $prize_4);\n                @include themable_prize_row(prize-theme-5, $prize_5);\n                @include themable_prize_row(prize-theme-6, $prize_6);\n                cursor: pointer;\n                &.currentUtl {\n                    .MuiTableCell-root {\n                        $border: 2px solid #000;\n                        border-top: $border;\n                        border-bottom: $border;\n                        &:first-of-type {\n                            border-right: $border;\n                        }\n                        &:last-of-type {\n                            border-left: $border;\n                        }\n\n                    }\n                }\n                &:first-of-type {\n                    .scoreCell-container {\n                        .scoreCell-added {\n                            box-shadow: rgb(60 64 67 / 50%) 0px 1px 6px 1px, rgb(60 64 67 / 15%) 0px 2px 6px 2px;\n                        }\n                    }\n                }\n            }\n            .MuiTableCell-root {\n                font-size: 16px;\n                border-bottom-color: rgba(0,0,0,0.1);\n                &.rankChangeCell {\n                    width: 0px;\n                    padding-left: 0px;\n                    padding-right: 8px;\n                }\n                &.rankCell {\n                    width: 0px;\n                    padding-right: 8px;\n                