\n\n\n\n.LB-GamesView {\n    margin: auto;\n    width: 600px;\n    max-width: 100%;\n    .LB-GameGumblersList {\n        @include floatingFrame;\n        border-radius: 12px;\n        overflow: hidden;\n        width: 500px;\n        max-width: 100%;\n        margin: 24px auto;\n        \n        .LB-GameHeader {\n            @include flexRow;\n            justify-content: space-between;\n            @include themable_game_header(tournament-theme, $tournament_1);\n            @include themable_game_header(tournament-theme-1, $tournament_1);\n            @include themable_game_header(tournament-theme-2, $tournament_2);\n            @include themable_game_header(tournament-theme-3, $tournament_3);\n            @include themable_game_header(tournament-theme-4, $tournament_4);\n            @include themable_game_header(tournament-theme-5, $tournament_5);\n            .MuiTableContainer-root {\n                box-shadow: none;\n                border-radius: 0px;\n                padding: 8px 0px;\n                .MuiTable-root {\n                    .MuiTableCell-root {\n                        text-align: center;\n                        padding: 4px;\n                        padding-bottom: 0px;\n                        color: white;\n                        border: none;\n                        &.delimitierHeader {\n                            font-size: 24px;\n                        }\n                        &.teamDisplayCell {\n                            width: 50%;\n                            vertical-align: top;\n                            padding: 8px;\n                            padding-top: 0px;\n                        }\n                        &.dateRowCell {\n                            $border: 2px solid #eeeeee3b;\n                            border-top: $border;\n                            border-bottom: $border;\n                            padding: 4px;\n                            font-size: 16px;\n                            line-height: 1.5;\n                        }\n                        &.scoreCell {\n                            font-size: 20px;\n                            line-height: 1.2;\n                        }\n                    }\n                    .MuiTableBody-root {\n                        .MuiTableRow-root:first-of-type {\n                            border-top: 2px solid #eeeeee3b;\n                        }\n                    }\n                }\n            }\n        }\n        .GameGumblersList-expandIconContainer {\n            @include centerContainer;\n            cursor: pointer;\n            .arrowDownIcon {\n                transition: transform 0.3s;\n                margin: 8px;\n                font-size: 32px;\n            }\n        }\n        &.GameGumblersList-expanded {\n            .GameGumblersList-expandIconContainer {\n                border-top: 2px solid rgba(0,0,0, 0.1);\n                .arrowDownIcon {\n                    transform: rotate(180deg);\n                }\n            }\n        }\n    }\n    .LB-DoneGamesView {\n        .DoneViewGames-pagination {\n            width: fit-content;\n        }\n    }\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {\n    text-shadow: -3px 2px 8px black;\n    color: white;\n}\n\n@mixin floatingTable {\n    background-color: rgb(255 255 255 / 95%);\n    border-radius: 12px;\n}","$basic-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;\n$medium-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;"],"sourceRoot":""}]);
